body {
    margin: 0;
    font-family: Arial, sans-serif;
    color: #333;
    background-color: #f0f0f5; /* Light grey background */
  }
  
  /* Title */
  .title {
    text-align: center;
    margin: 20px 0;
    font-size: 24px;
    color: #4b4b7c;
  }
  
  /* Activity Table */
  .activity-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
  }
  
  .activity-table th, .activity-table td {
    text-align: left;
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .activity-table th {
    background-color: #4b4b7c; /* Dark blue table header */
    color: white;
  }
  
  .activity-table tr:nth-child(even) {
    background-color: #f9f9f9; /* Light grey even rows */
  }
  
  .activity-table tr:hover {
    background-color: #eaeaea; /* Hover effect */
  }
  
  /* Logout Container */
  .logout-container {
    text-align: center;
    margin: 20px 0;
  }
  
  .logout-button {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .logout-button:hover {
    background-color: #e60000;
  }
  